@import '@/theme/variables.scss';
@import '@/shared/functions.scss';

.input-number {
  display: inline-flex;
  align-items: center;

  &__btn,
  &__input {
    width: calc-rem(30);
    height: calc-rem(30);
    text-align: center;
  }

  &__input {
    font-weight: 700;
    font-size: calc-rem(12);
    text-transform: uppercase;
    line-height: 1.25;
    padding: 0 3px;
    border: 0;
    background: none;
    -moz-appearance: textfield;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      display: none;
    }

    &:read-only {
      outline: none;
    }
  }

  &__btn {
    border-radius: 100vh;
    border: 1px solid $dark25;
    background: $white;
    line-height: calc-rem(30);
    color: $dark25;
    cursor: pointer;

    &:disabled {
      cursor: default;
      opacity: 0.38;
    }
  }
}
